import { Link, useNavigate } from "react-router-dom";
import { useState } from "react";
import { useEffect } from "react";

import { useContext } from "react";
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';

import Snackbar from '@mui/material/Snackbar';

import Table from 'react-bootstrap/Table';
import Box from '@mui/material/Box';

import API from "./API";
import LaunchIcon from '@mui/icons-material/Launch';
import AssignmentIcon from '@mui/icons-material/Assignment';
import noteContext from './NoteContext';

function Home() {
    const [data, setData] = useState([]);
    const [error, setError] = useState("%");
    const [loadingbar, setHideLoading] = useState(false);

    const [student_class, setClass] = useState(1);
    const [student_name, setStudentName] = useState();
    const [grade, setGrade] = useState();
    const [sr, setSR] = useState();
    const navigate = useNavigate();
    const a = useContext(noteContext);
    let token = a.token;

    const set_grade = (value) => {
        setSR("");

        setGrade(value);
        setStudentName("");


    }

    const set_class_name = (value) => {
        setSR("");

        setClass(value);
        setStudentName("");


    }
    const set_Student_Name = (value) => {

        setSR("");

        setClass("");
        setStudentName(value);
        setGrade("");
    };

    const setSRnumber = (value) => {

        setSR(value);

        setClass("");
        setStudentName("");
        setGrade("");
    }



    const fetchAPIData = async () => {




        var search_query = {
            "class": student_class,
            "grade": grade,
            "sr": sr,
            "student_name": student_name
        }

        console.log(search_query);

        const res = await API.post("search_students", search_query, {
            headers: {
                Accept: 'application/json',
                Authorization: 'Bearer ' + token
            }
        });
        setData(res.data);

        setHideLoading(true);



    };

    useEffect(() => {

        if (a.login_status === null)
            navigate("/login")


        else {

            //  fetchAPIData();


        }



    }, []);








    return (

        <div className="sakshmb">


            <div className="mb-3">



                <Box
                    component="form"
                    sx={{
                        '& > :not(style)': { m: 1, width: '25ch' },
                    }}
                    noValidate
                    autoComplete="off"
                >


                   Student class
                    <input
                        name='Type Class'
                        placeholder='Type Class'
                        value={student_class}
                        onChange={(e) => set_class_name(e.target.value)}




                    />
                    Grade
                    <input
                        name='GRADE'
                        placeholder='Grade'

                        onChange={(e) => set_grade(e.target.value)}
                        value={grade}



                    />
                   SR
                    <input
                        name='SR'
                        placeholder='SR'

                        onChange={(e) => setSRnumber(e.target.value)}


                        value={sr}

                    />
                 Student name
                    <input

                        placeholder='Student Name'


                        value={student_name}

                        onChange={(e) => set_Student_Name(e.target.value)}


                    />


                    <Button onClick={fetchAPIData} variant="contained">Search Students</Button>

                </Box>
            </div>








            {data.length > 0  && 

<Table striped responsive bordered hover size="sm">


                <thead>

                    <tr>
                        <th>SR</th>
                        <th>Class</th>
                        <th>Grade</th>
                        <th>Name</th>
                        <th>Father name</th>
                        <th>Mother Name</th>
                        <th>

                            Action
                        </th>
                    </tr>
                </thead>
                <tbody>

                    {data.map((item, index) => (


                        <tr key={index}>
                            <td>   {item.sr} </td>

                            <td>   {item.class} </td>
                            <td>    {item.grade} </td>

                            <td>   {item.name} </td>



                            <td>   {item.father_name} </td>

                            <td>   {item.mother_name} </td>


                            <td>

                                <Link target="_blank"   to={`/student/${item.sr}`}> <LaunchIcon /></Link>
                            
                                <Link  target="_blank"  to={`/homework/${item.sr}`}><AssignmentIcon/> </Link>


 

 

 
 




                            </td>
                        </tr>
                    ))}
                </tbody>
            </Table>
}

        </div>

    )
}

export default Home;